body {
  font-family: "Microsoft Yahei";
  background-color: #fff !important;
}
.v-cloak {
  display: none;
}
.h-block .h-title {
  height: 1.333333rem;
  background-color: #5C4329;
  color: #fff;
  line-height: 1.333333rem;
  position: relative;
}
.h-block .h-title .h-text {
    font-size: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 45%;
    display: block;
}
[data-dpr="2"] .h-block .h-title .h-text {
      font-size: 40px;
}
[data-dpr="3"] .h-block .h-title .h-text {
      font-size: 60px;
}
.h-block .h-title .h-entry {
    position: absolute;
    right: 0.4rem;
    line-height: normal;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.h-block .h-title .h-entry img {
      height: 0.56rem;
}
.h-block .h-title .h-entry .h-entry-text {
      display: block;
      color: #fff;
}
.h-block .h-banner {
  height: 2.6rem;
  background-image: url(./assets/images/banner-bg@2x.png);
  overflow: hidden;
  background-size: 100%;
}
.h-block .h-banner .h-userInfo {
    height: 1.2rem;
    background-color: rgba(128, 128, 128, 0.3);
    margin-top: 1.413333rem;
    padding: 0 0.4rem;
}
.h-block .h-banner .h-userInfo .buyerNick {
      margin-top: 0.2rem;
      float: left;
      color: #fff;
}
.h-block .h-banner .h-userInfo .buyerNick .buyerNick-img {
        background-image: url(./assets/images/cj@2x.png);
        width: 0.933333rem;
        height: 0.933333rem;
        background-size: 100%;
        display: inline-block;
        vertical-align: middle;
        border-radius: 50%;
}
.h-block .h-banner .h-userInfo .buyerNick .buyerNick-text {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 3rem;
        display: inline-block;
}
.h-block .h-banner .h-userInfo .points {
      float: right;
      color: #000;
      height: 0.8rem;
      background-color: #FCB904;
      border-radius: 0.4rem;
      margin-top: 0.2rem;
      line-height: 0.8rem;
      padding-left: 0.266667rem;
}
.h-block .h-banner .h-userInfo .points img {
        vertical-align: middle;
}
.h-block .h-banner .h-userInfo .points .points-num {
        padding-right: 0.16rem;
        display: inline-block;
}
.h-block .h-btnGroup {
  height: 1.066667rem;
  background-color: #FF5101;
}
.h-block .h-btnGroup .link {
    width: 49%;
    display: inline-block;
    border-left: 1px solid #fff;
    font-size: 17px;
    color: #fff;
    text-align: center;
    line-height: 1.066667rem;
}
[data-dpr="2"] .h-block .h-btnGroup .link {
      font-size: 34px;
}
[data-dpr="3"] .h-block .h-btnGroup .link {
      font-size: 51px;
}
.h-block .h-btnGroup .link img {
      vertical-align: middle;
}
.no-items {
  padding-top: 0.7rem;
  background-color: #ffffff;
}
.no-items .img {
    width: 3.413333rem;
}
.no-items .remind-text {
    margin: 0.4rem 0;
}
.link-a {
  color: #fff;
}
.link-a:hover, .link-a:active {
    color: #fff;
}
a:active, a:hover, a:focus {
  text-decoration: none !important;
}
.dg-dialog .weui-dialog {
  max-width: 6.8rem;
  width: 6.8rem;
  padding: 1.066667rem 0.6rem 0.4rem 0.6rem;
  border-radius: 0.2rem;
  box-sizing: border-box;
  overflow: visible;
}
.dg-dialog .weui-dialog .dialog-content {
    color: #333;
    font-size: 0.373333rem;
    margin-bottom: 0.8rem;
}
.dg-dialog .weui-dialog .dialog-btn {
    display: inline-block;
    height: 0.8rem;
    line-height: 0.8rem;
    padding-left: 0.6rem;
    padding-right: 0.6rem;
    border-radius: 0.133333rem;
    background: #ff590d;
    border: none;
    color: #fff;
    font-size: 0.373333rem;
}
.dg-dialog .weui-dialog .dialog-btn:hover, .dg-dialog .weui-dialog .dialog-btn:active {
      text-direction: none;
}
.dg-dialog .weui-dialog .dialog-btn-close {
    display: block;
    position: absolute;
    top: -1.186667rem;
    right: 0;
    height: 1.186667rem;
    width: 0.8rem;
    background: url(./assets/images/btn-close.png) no-repeat;
    background-size: cover;
}
.dg-confirm .weui-dialog {
  width: 6.8rem;
  max-width: 6.8rem;
  border-radius: 0.2rem;
  box-sizing: border-box;
}
.dg-confirm .weui-dialog .weui-dialog__bd {
    padding-top: 1.066667rem;
    font-size: 0.373333rem;
    padding-bottom: 0.666667rem;
}
.dg-confirm .weui-dialog .weui-dialog__ft {
    padding-left: 0.506667rem;
    padding-right: 0.506667rem;
    padding-bottom: 0.333333rem;
    padding-top: 0.333333rem;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.dg-confirm .weui-dialog .weui-dialog__ft:after {
      border-top: none;
}
.dg-confirm .weui-dialog .weui-dialog__ft .weui-dialog__btn {
      height: 0.8rem;
      line-height: 0.8rem;
      font-size: 0.333333rem;
      width: 2.666667rem;
      color: #fff;
      -webkit-box-flex: 0;
      -webkit-flex: none;
              flex: none;
      border-radius: 0.133333rem;
}
.dg-confirm .weui-dialog .weui-dialog__ft .weui-dialog__btn_default {
      background: #fe9d01;
}
.dg-confirm .weui-dialog .weui-dialog__ft .weui-dialog__btn_primary {
      background: #ff590d;
}

/* -------------通用遮罩------------- */
.cover {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(50, 50, 50, 0.95);
  z-index: 1;
}

/* -------------通用toast------------- */
.toast {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 5.253333rem;
  height: 1.066666rem;
  border-radius: 0.053333rem;
  color: #fff;
  line-height: 1.066666rem;
  font-size: 0.373333rem;
  background-color: #808080;
  z-index: 3;
}

/* -------------dialog样式------------- */
.dialog {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 6.8rem;
  border-radius: 0.266666rem;
  background-color: #fff;
  font-size: 14px;
  z-index: 2;
}
[data-dpr="2"] .dialog {
    font-size: 28px;
}
[data-dpr="3"] .dialog {
    font-size: 42px;
}
.dialog .btn-cancel {
    top: -0.8rem;
    right: 0;
}
.dialog .btn-cancel img {
      height: 0.8rem;
}
.dialog.act-ruler {
    width: 8.8rem;
}
.dialog.act-ruler h2 {
      padding: 0.666666rem 0;
      line-height: 1;
}
.dialog.act-ruler ul {
      padding: 0.466666rem 0.4rem;
      text-align: left;
}
.dialog.failed h2 {
    margin: 1rem 0 0.666666rem;
    padding: 0 0.426666rem;
}
.dialog.failed button {
    display: block;
    width: 3.466666rem;
    height: 0.8rem;
    margin: 0 auto 0.533333rem;
    border-radius: 0.133333rem;
    outline: none;
    border: none;
    font-size: 14px;
    color: #fff;
    background-color: #ff590d;
}
[data-dpr="2"] .dialog.failed button {
      font-size: 28px;
}
[data-dpr="3"] .dialog.failed button {
      font-size: 42px;
}
.dialog.prize-active {
    background: transparent;
}
.dialog.prize-active .btn-cancel {
      top: -0.2rem;
      right: 0.586666rem;
      z-index: 2;
}
.go-shop {
  background-color: #fe9d01;
  display: inline-block;
  width: 2.066667rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.133333rem;
  color: #fff;
  font-size: 0.373333rem;
  margin-bottom: 0.4rem;
}
.go-use {
  background-color: #ff590d;
  display: inline-block;
  width: 2.066667rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.133333rem;
  color: #fff;
  font-size: 0.373333rem;
  margin-bottom: 0.4rem;
}
.prize-active {
  width: 6.853333rem;
  box-sizing: border-box !important;
  padding-left: 3.41rem;
  padding-top: 1.25rem;
  background-color: transparent !important;
}
.prize-active header {
    top: 0;
    left: 0;
    width: 100%;
    height: 1.573333rem;
    background: url("./assets/images/activity/zj.png");
    background-size: 100% 100%;
    font-size: 18px;
    z-index: 1;
}
[data-dpr="2"] .prize-active header {
      font-size: 36px;
}
[data-dpr="3"] .prize-active header {
      font-size: 54px;
}
.prize-active header h2 {
      margin-top: 0.293333rem;
}
.prize-active main {
    box-sizing: border-box;
    left: 50%;
    top: 1.24rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 5.733333rem;
    margin: 0 auto;
    padding-top: 0.466666rem;
    background-color: #fff2c5;
}
.prize-active main .prize-notice {
      padding: 0.466666rem 0;
      font-size: 14px;
      line-height: 1;
}
[data-dpr="2"] .prize-active main .prize-notice {
        font-size: 28px;
}
[data-dpr="3"] .prize-active main .prize-notice {
        font-size: 42px;
}
.prize-active main .prize-noticeone {
      padding: 0.466666rem 0;
      padding-bottom: 0.133333rem;
      font-size: 14px;
      line-height: 1;
}
[data-dpr="2"] .prize-active main .prize-noticeone {
        font-size: 28px;
}
[data-dpr="3"] .prize-active main .prize-noticeone {
        font-size: 42px;
}
.prize-active main .prize-subnotice {
      text-align: left;
      font-size: 12px;
      color: #f34d4d;
      padding: 0 0.533333rem;
      margin-bottom: 0.466667rem;
}
[data-dpr="2"] .prize-active main .prize-subnotice {
        font-size: 24px;
}
[data-dpr="3"] .prize-active main .prize-subnotice {
        font-size: 36px;
}
.prize-active main .btn {
      padding: 0 0.533333rem 0.4rem;
}
.prize-active main .btn button {
        width: calc((100% - 0.533333rem)/2);
        outline: none;
        border: none;
        border-radius: 0.133333rem;
        color: #fff;
        font-size: 0.373333rem;
        line-height: 0.8rem;
}
.prize-active main .btn .use-method {
        background-color: #ff590d;
        margin-left: 0.533333rem;
}

/* -------------活动中心按钮------------- */
.btn-act-center {
  background-color: #fff;
  left: 0;
  color: #f34d4d;
  padding: 0 0.2rem 0 0.2rem;
  border-radius: 0 0.35rem 0.35rem 0;
  top: 0.333333rem;
  font-size: 14px;
  line-height: 0.7rem;
  z-index: 1;
}
[data-dpr="2"] .btn-act-center {
    font-size: 28px;
}
[data-dpr="3"] .btn-act-center {
    font-size: 42px;
}

/* -------------活动规则按钮------------- */
.btn-act-rules {
  right: 0;
  padding-right: 0.2rem;
  color: #fff;
  top: 0.333333rem;
  font-size: 14px;
  line-height: 0.7rem;
  z-index: 1;
}
[data-dpr="2"] .btn-act-rules {
    font-size: 28px;
}
[data-dpr="3"] .btn-act-rules {
    font-size: 42px;
}

/* -------------积分规则按钮------------- */
.btn-point-rules {
  background-color: #ff9000;
  right: 0;
  color: #fff;
  padding: 0 0.2rem 0 0.2rem;
  border-radius: 0.35rem 0 0 0.35rem;
  top: 0.333333rem;
  font-size: 14px;
  line-height: 0.7rem;
  z-index: 1;
}
[data-dpr="2"] .btn-point-rules {
    font-size: 28px;
}
[data-dpr="3"] .btn-point-rules {
    font-size: 42px;
}

/*-----------奖品图片样式-------------*/
.vux-popup-dialog.vux-popup-picker.vux-popup-bottom .vux-popup-header {
  height: 0.933333rem;
  line-height: 0.933333rem;
  font-size: 0.373333rem;
}
.vux-popup-dialog.vux-popup-picker.vux-popup-bottom .scroller-item {
