}
.vux-popup-dialog.vux-popup-picker.vux-popup-bottom .text {
  background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6)), -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
  background-image: linear-gradient(top, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6)), -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
}
.weui-loading_toast .weui-toast {
  margin-top: -90px;
  top: 50%;
}
.vux-loading .weui-toast {
  z-index: 5000;
}
